%ic {
	font-family: "icons";
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}
[class*="icon-"] {
	&:before {
		@extend %ic;
		display: block;
	}
}


._icon-Chat:before {
	content: "\e900";
  }
  ._icon-iPhone:before {
	content: "\e901";
  }
  ._icon-arrow:before {
	content: "\e902";
  }
  ._icon-arrowUp:before {
	content: "\e903";
  }
  